let $menu__burger = document.querySelector(".menu__burger");
let $options = document.querySelectorAll(".courants");

let burger = document.querySelector(".menu");

// API FETCH
function getMoviesById(arr) {
  fetch(`${API_URL}/movie/${arr}?api_key=${API_KEY}`)
    .then((response) => response.json())
    .then((response) => showMoviesbyId(response));
}

// BOUCLE SELECTION
for (let a = 0; a < $options.length; a++) {
  $options[a].addEventListener("click", function (e) {
    $main.innerHTML = "";

    for (let i = 0; i < arr[a][i]; i++) {
      getMoviesById(arr[a][i]);
      $wholeMenu.classList.remove("menu-burger-is-active");
      $loader.classList.add("is-active");
      $blur.classList.add("is-hidden");
    }
  });
}

// SHOWMOVIES
function showMoviesbyId(movies) {
  $loader.classList.remove("is-active");
  $count.innerText = "";
  $btnPage.classList.add("is-hidden");

  let $mainContainer = document.createElement("div");
  let $movieTitle = document.createElement("h2");
  let $movieImage = document.createElement("img");
  let $movieText = document.createElement("p");

  $mainContainer.classList.add("main-container");
  $movieImage.classList.add("main-container-image");
  $movieText.classList.add("main-container-text");

  $main.appendChild($mainContainer);
  $mainContainer.appendChild($movieTitle);
  $mainContainer.appendChild($movieImage);
  $mainContainer.appendChild($movieText);

  $movieTitle.textContent = movies.title;
  $movieImage.setAttribute("src", IMAGES_URL + movies.poster_path);

  $movieText.textContent = movies.overview;
  //$movieMoreInfo.innerText = "More information";
  $movieImage.addEventListener("click", function () {
    $player.classList.add("player-is-active");
    $playerTitle.innerText = movies.title;
    $playerSynopsis.innerText = movies.overview;
    $playerImage.setAttribute("src", IMAGES_URL + movies.poster_path);
    $playerLanguage.innerText =
      "langue d'origine : " + movies.original_language;
    $playerReleaseDate.innerText = "date de sortie : " + movies.release_date;
  });

  $lastVisiblePage = movies.total_pages;
  $counterPage();
  $returnTopPage();
}
